@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    color: #2e8b57;
    font-weight: 400;
    font-family: 'Noto Sans KR', sans-serif;
}

.todo-list-template {
    margin: 4rem auto;
    width: 50%;
    background-color: #f0e68c;
    border: 3px solid #2e8b57;
}

.title {
    padding: 2rem;
    color: #f0e68c;
    /* color: #2e8b57; */
    font-size: 2.5rem;
    font-weight: 900;
    text-align: center;
    background-color: #2e8b57;
    border-bottom: 3px solid #2e8b57;
}

.form-wrapper {
    padding: 1rem;
    border-bottom: 3px solid #2e8b57;
}

.todos-wrapper {
    padding: 1rem;
    min-height: 5rem;
}

.form {
    display: -webkit-flex;
    display: flex;
}

.form input {
    -webkit-flex: 1 1;
            flex: 1 1; /* 버튼을 뺀 빈 공간을 모두 채워줍니다 */
    font-size: 1.25rem;
    outline: 0;
    border: 0;
    background-color: rgba(46,139,87,0.2);
}

.create-button {
    margin-left: 1rem;
    padding: 0.5rem;
    color: #f0e68c;
    font-weight: bold;
    background-color: #2e8b57;
    cursor: pointer;
}

.todo-item {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 1rem;
    transition: all 0.15s;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}

.todo-item:hover {
    background-color: #2e8b57;
}

.todo-item:hover div {
    color: #f0e68c;
}

/* todo-item 에 마우스가 있을때만 .remove 보이기 */
.todo-item:hover .remove {
    opacity: 1;
}

/* todo-item 사이에 윗 테두리 */
.todo-item + .todo-item {
    border-top: 1px solid #2e8b57;
}

.remove {
    margin-right: 1rem;
    color: #e64980;
    font-weight: 600;
    opacity: 0;
}

.todo-text {
    -webkit-flex: 1 1;
            flex: 1 1; /* 체크, 엑스를 제외한 공간 다 채우기 */
    word-break: break-all;
}

.checked {
    text-decoration: line-through;
    color: #2e8b57;
}

.check-mark {
    font-size: 1.5rem;
    line-height: 1rem;
    margin-left: 1rem;
}

